export default {
	data(){
		return {
			ladingList: [],
		}
	},
	onShow(){
		this.getLadingList();
	},
	methods: {
		//获取提货单列表
		getLadingList() {
			this.$api.storeLadingList({}, res => {
				this.ladingList = res.data;
			});
		},
		
		//提货单核销
		ladingWrite(id) {
			this.$common.navigateTo('./index?id=' + id);
		},

		//删除
		ladingDel(id) {
			this.$common.modelShow('提示', '删除提货单后将无法找回！', res => {
				let data = {
					'lading_id': id
				}
				this.$api.ladingDel(data, res => {
					this.$common.successToShow(res.msg, res => {
						this.getLadingList();
					});
				});
			});
		}
	}
}
